
import { defineComponent } from 'vue'
import { Option } from '../Forms/FieldInterface'
import FieldMixinVue from './FieldMixin.vue'
import ViewPort from '../DataViews/ViewPort.vue'
import { 
    IonCheckbox,
    IonInput,
    IonButton,
    IonIcon,
    IonCol,
    IonRow,
} from "@ionic/vue"
import {
    arrowUp,
    arrowDown
} from "ionicons/icons"
import { getNumberOrdinal } from "@/utils/Strs"

export default defineComponent({
    name: "HisSelect",
    mixins: [FieldMixinVue],
    components: {
        IonInput,
        IonButton,
        IonCheckbox,
        IonCol,
        IonRow,
        IonIcon,
        ViewPort,
    },
    setup() {
        return {
            arrowUp,
            arrowDown
        }
    },
    data: () => ({
        listData: [] as Option[]
    }),
    mounted() {
        this.init()
    },
    activated() {
        this.init()
    },
    methods: {
        async init() {
            this.$emit('onFieldActivated', this)
            if (typeof this.config.pregnancyCount === 'function') {
                this.listData = this.buildOptions((await this.config.pregnancyCount(this.fdata)))
                this.$emit('onValue', this.listData)
            }
        },
        buildOptions(limit: number) {
            const options = []
            for(let i=0; i < limit; ++i) {
                const num = i + 1 
                options.push({
                    label: `${num} <sup>${getNumberOrdinal(num)}</sup>`,
                    value: 1,
                    isChecked: true
                })
            }
            return options
        }
    },
    watch: {
        listData: {
            handler(data: Option[]) {
                if (data) this.$emit('onValue', data)
            },
            deep: true,
            immediate: true
        }
    }
})
